<script>
import axios from 'axios';
import { useRoute } from 'vue-router';
export default {
  data: () => ({
    recipeForm: false,
    ingredientForm: false,
    instructionsForm: false,
    email: null,
    password: null,
    loading: false,
    recipe: {
      name: '',
      shortDescription: '',
      description: '',
      categories: [],
      difficult: [],
      time: '',
      servings: '',
      ingredients: [
        {
          amount: 100,
          units: 'Gramos',
          name: 'Arroz',
          allergens: ['Ninguno']
        }
      ],
      instructions: [
        {
          step: 1,
          description: 'Instrucciones paso 1'
        }
      ],
      sides: '',
      smallImage: '',
      mainImage: '',
      videoLink: ''
    },
    ingredient: {
      amount: '',
      units: '',
      name: '',
      allergens: []
    },
    instruction: {
      step: 0,
      description: ''
    },
    helpers: {
      categories: ['Desayunos', 'Ensaladas', 'Platos fuertes', 'Guarniciones', 'Pastas', 'Sopas', 'Salsas', 'Bebidas', 'Pan', 'Postres', 'FastFood'],
      difficult: ['Dificultad baja', 'Dificultad media', 'Dificultad alta'],
      ingredientsUnits: ['Gramos', 'Kilos', 'Mililitros', 'Litros', 'Cucharaditas', 'Cucharadas'],
      allergens: ['Gluten', 'Huevos', 'Cacahuetes', 'Soja', 'Lácteos', 'Ninguno'],
      cookware: [
        {
          text: 'Cuchillo',
        },
        {
          text: 'Sarten',
        },
        {
          text: 'Olla profunda',
        },
        {
          text: 'Tabla',
        },
        {
          text: 'Batidora',
        },
      ]
    },
    search: '',
    selected: [],
    recipes: {},
    recipeId: null,
    isEdit: false
  }),

  computed: {
    allSelected() {
      return this.selected.length === this.helpers.cookware.length
    },
    cookware() {
      const search = this.search.toLowerCase()

      if (!search) return this.helpers.cookware

      return this.helpers.cookware.filter(item => {
        const text = item.text.toLowerCase()

        return text.indexOf(search) > -1
      })
    },
    selections() {
      const selections = []

      for (const selection of this.selected) {
        selections.push(selection)
      }

      return selections
    }
  },

  watch: {
    selected() {
      this.search = ''
    },
  },

  methods: {
    checkRecipeId() {

    },
    async loadEditingRecipeData(recipeId) {
      try {
        let url = `http://localhost:8080/recipes/${recipeId}`;
        // Realizando la petición GET con Axios
        const response = await axios.get(url, {
          headers: {
            'Content-Type': 'application/json'
          },
          // Configuración adicional para CORS si fuera necesario
          withCredentials: false // No enviar cookies ni autenticación
        });
        
        // Asignando los datos obtenidos al array `recipes`
        this.recipe = response.data;
        console.log(this.recipe);
      } catch (error) {
        this.error = 'Error fetching recipes'; // Manejar posibles errores
        console.error(error);
      }
    },
    onSubmit() {
      alert('Envío del formulario');
      if (!this.recipe) return

      this.axios.post('http://localhost:8080/recipes', this.recipe)
        .then(function (response) {
          console.log(response);
        })
        .catch(function (error) {
          console.log(error);
        })
        .finally(function () {
          this.loading = false
        });

    },
    required(v) {
      return !!v || 'Field is required'
    },
    addIngredient() {
      let ingredient = this.ingredient;
      alert(ingredient);
    },
    addInstruction() {
      let instruction = this.instruction;
      alert(instruction);
    },
    clearForm() {
      this.$refs.recipeForm.reset()
    },
    validateRecipeForm() {
      alert('Envío del formulario');
    }
  },

  mounted(){
    const route = useRoute();
    
    if(route.params.id){
      this.recipeId = route.params.id;
      this.isEdit = true;
      this.loadEditingRecipeData(this.recipeId);
    }
  }
}
</script>

